import React from "react"
import Layout from "../components/layout"
import { graphql } from "gatsby"
import ServiceArea from "../components/construct/ServiceArea"
import SliderContent from "../components/construct/Slider"
import Seo from "../components/seo"
import About from "../components/construct/About"
import Hire from "../components/construct/Hire"
import Quote from "../components/construct/Quote"
import Partners from "../components/construct/Partners"

export const Head = () => <Seo title="Servicios eléctricos integrales para la industria, la construcción y las pymes" />

const AboutUsPage = ({ data }) => {


  return (
    <Layout>
      <About />
      <Hire />
    </Layout>
  )
}

export const query = graphql`
  query {

    servicios: allServiciosJson {
      nodes {
        title
        description
        href
        image {
          childImageSharp {
            gatsbyImageData(
                placeholder: BLURRED
                formats: [AUTO, WEBP, AVIF]
                width: 370
                height: 188
              )
          }
        }
      }
    }
  
 
    allPagesJson(filter: { idPage: { eq: "index" } }) {
      nodes {
        idPage
        content {
          category
          description
          title
        }
      }
    }
  }
`

export default AboutUsPage
